// =============================================================================
// Zotefoams Critical Bundle - Immediate functionality for above-the-fold experience
// Loads in <head> for instant interactivity
// =============================================================================

// CRITICAL: Touch detection MUST run before navigation.js
// Navigation relies on touch-device class being present
import { ZotefoamsDeviceUtils } from './utils/dom-utilities.js';

// Initialize touch detection as soon as body is available
// This runs before navigation.js by using blocking execution
if (document.body) {
    // Body already exists (unlikely in head, but handle it)
    ZotefoamsDeviceUtils.initTouchSupport();
} else {
    // Body doesn't exist yet, use DOMContentLoaded
    // This will execute BEFORE navigation.js because we're first in the script
    document.addEventListener('DOMContentLoaded', () => {
        ZotefoamsDeviceUtils.initTouchSupport();
    });
}

// Import critical navigation (depends on touch detection)
import './critical/navigation.js';

// Critical bundle initialization